import React, { useState } from 'react'
import { Bar } from 'react-chartjs-2';
import './css/cssPreBarGrafy.css'

function BarGraf({ props }) {
    const [allData, setAllData] = useState({
        dataSetsCases: {
            labels: props.datumy,
            datasets: [{
                label: 'Počet mŕtvych',
                backgroundColor: "grey",
                data: props.dataSetsDeaths.datasets[0].data,
            },
            {
                label: 'Počet vyliečených',
                backgroundColor: "#57C757",
                data: props.dataSetsRecovered.datasets[0].data,
            },
            {
                label: 'Počet nakazených',
                backgroundColor: "#FF4D4D",
                data: props.dataSetsCases.datasets[0].data,
            }],
        },

        dataSetsDeaths: {
            labels: props.datumy,
            datasets: props.dataSetsDeaths.datasets
        },


        dataSetsRecovered: {
            labels: props.datumy,
            datasets: props.dataSetsRecovered.datasets
        },

        dataSetsActiveCases: {
            labels: props.datumy,
            datasets: props.dataSetsActiveCases.datasets
        },

        dataSetsDailyCases: {
            labels: props.datumy,
            datasets: props.dataSetsDailyCases.datasets
        },

        options2: {
            options: {
                // legendCallback: function (chart) {
                //     console.log(chart)
                // },
                maintainAspectRatio: false,
                responsive: true,
                legend: {
                    position: "bottom",
                    labels: {
                        padding: 25,
                    },
                    // onClick: function (e, legendItem) {
                    //     console.log(legendItem)
                    //     return e

                    // }
                },
                scales: {
                    xAxes: [{
                        stacked: true,
                        gridLines: {
                            display: false,
                        }
                    }],
                    yAxes: [{
                        stacked: true,
                        ticks: {
                            callback: function (value) {
                                return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")
                            }
                        }
                    }]
                },
                tooltips: {
                    mode: 'x',
                    callbacks: {
                        label: function (tooltipItem, chart) {
                            var datasetLabel = chart.datasets[tooltipItem.datasetIndex].label || '';

                            return datasetLabel + ": " + tooltipItem.yLabel.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")
                        }
                    },
                },
                animation: {
                    duration: 500,
                    onProgress: function () {
                        var chartInstance = this.chart,
                            ctx = chartInstance.ctx;
                        ctx.textAlign = 'center';
                        ctx.fillStyle = "rgba(0, 0, 0, .75)";
                        ctx.textBaseline = 'bottom';
                        if (window.innerWidth > 1220) {
                            ctx.font = "bold 12px Arial";
                        }
                        else if (window.innerWidth > 720) {
                            ctx.font = "bold 11px Arial";
                        }
                        else {
                            ctx.font = "bold 6px Arial";
                        }
                        // if (window.innerWidth > 720) {
                        this.data.datasets.forEach(function (dataset, i) {
                            var meta = chartInstance.controller.getDatasetMeta(i);
                            meta.data.forEach(function (bar, index) {
                                var data = dataset.data[index];
                                if (index % 2 === 0) {
                                    ctx.fillText(FormatNumberShorter(data), bar._model.x + 0, bar._model.y - 4);
                                    // console.log(bar._model)
                                    // console.log(bar._model.x)
                                    // console.log(data, bar._model.x + 0, bar._model.y - 4)
                                    // console.log(Math.abs(data) > 999 ? Math.sign(data) * ((Math.abs(data) / 1000).toFixed(1)) + 'k' : Math.sign(data) * Math.abs(data))
                                }
                            })
                        })
                        // }
                    }
                },
            },

        },



        options: {
            maintainAspectRatio: false,
            responsive: true,
            legend: {
                position: "bottom",
                labels: {
                    padding: 25,
                }
            },
            scales: {
                xAxes: [{
                    stacked: true,
                    gridLines: {
                        display: false,
                    }
                }],
                yAxes: [{
                    stacked: true,
                    ticks: {
                        callback: function (value) {
                            return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")
                        }
                    }
                }]
            },
            tooltips: {
                mode: 'x',
                callbacks: {
                    label: function (tooltipItem, chart) {
                        var datasetLabel = chart.datasets[tooltipItem.datasetIndex].label || '';

                        return datasetLabel + ": " + tooltipItem.yLabel.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")
                    }
                },
            },
            animation: {
                duration: 10,
                // onProgress: function () {
                //     var chartInstance = this.chart,
                //         ctx = chartInstance.ctx;
                //     ctx.textAlign = 'center';
                //     ctx.fillStyle = "rgba(0, 0, 0, .75)";
                //     ctx.textBaseline = 'bottom';
                //     if (window.innerWidth > 1220) {
                //         ctx.font = "bold 12px Arial";
                //     }
                //     else if (window.innerWidth > 720) {
                //         ctx.font = "11px Arial";
                //     }
                //     if (window.innerWidth > 720) {

                //         this.data.datasets.forEach(function (dataset, i) {
                //             var meta = chartInstance.controller.getDatasetMeta(i);
                //             meta.data.forEach(function (bar, index) {
                //                 var data = dataset.data[index];
                //                 if (index % 2 === 0) {
                //                     ctx.fillText(data, bar._model.x + 0, bar._model.y - 4);
                //                     console.log(data)
                //                 }
                //             })
                //         })
                //     }
                // }
            },

        },

        // options: {
        //     responsive: true,
        //     maintainAspectRatio: false,

        //     scales: {
        //         xAxes: [{
        //             stacked: true,
        //             gridLines: {
        //                 display: false,
        //             }
        //         }],
        //         yAxes: [{
        //             stacked: true,
        //             ticks: {
        //                 beginAtZero: true,
        //             },
        //             type: 'linear',
        //         }]
        //     },

        // }


    })

    function FormatNumberShorter(x) {
        if (isNaN(x) || (x < 1000)) {
            // console.log("If1: " + x)
            return x
        }
        else if (x < 10000) {
            return (x / 1000).toFixed(2) + "k"
        }
        else if (x < 100000) {
            return (x / 1000).toFixed(1) + "k"
        }

        else if (x < 1000000) {
            return (x / 1000).toFixed(1) + "k"
        }
        else if (x < 10000000) {
            return (x / 100000).toFixed(1) + "M"
        }

        else if (x < 1000000000) {
            return (x / 1000000).toFixed(1) + "M"
        }

        else if (x < 1000000000000) {
            return Math.round((x / 1000000000)) + "B"
        }

    }

    return (
        <div className="">
            <div className="mainDivPreGrafy" >


                <div className="barsGrafy" >
                    <h1>Denný prírastok nakazených</h1>
                    <Bar
                        data={allData.dataSetsDailyCases}
                        options={allData.options2.options}
                    />
                </div>



                <div className="barsGrafy" >
                    <h1>Celkový počet aktívnych prípadov</h1>
                    <Bar
                        data={allData.dataSetsActiveCases}
                        options={allData.options2.options}
                    />
                </div>

                <div className="barsGrafy"  >
                    <h1>Celkový prírastok nakazených/vyliečených</h1>
                    <Bar
                        data={allData.dataSetsCases}
                        options={allData.options2.options}
                    />
                </div>

                {/* <div className="barsGrafy" >
                    <h1>Celkový počet vyliečených</h1>
                    <Bar
                        data={allData.dataSetsRecovered}
                        options={allData.options}
                    />
                </div>

                <div className="barsGrafy" >
                    <h1>Celkový počet úmrtí</h1>
                    <Bar
                        data={allData.dataSetsDeaths}
                        options={allData.options}
                    />
                </div> */}
            </div>
        </div>
    )
}

export default BarGraf
